<template>
    <div class="container">
        <el-row>
            <el-card shadow="hover">
                <span class="buddha-score-title">: </span><span class="buddha-score">{{ scoreSum }}</span>
                <el-button type="primary" round @click="onClickReturn" class="buddha-return"></el-button>
            </el-card>
        </el-row>
        <div class="buddha-row-tabs">
            <el-tabs tab-position="left" value="chart" @tab-click="onClickTab">
                <el-tab-pane label="" name="chart">
                    <div id="buddha-chart-pie" v-resize="onChartPieResize"></div>
                    <div id="buddha-chart" v-resize="onChartResize"></div>
                </el-tab-pane>
                <el-tab-pane label="" name="manager">
                    <el-collapse accordion>
                        <el-collapse-item>
                            <template slot="title">
                                <i class="header-icon fa fa-plus-square"></i>
                            </template>
                            <el-form ref="form" :model="form" label-width="80px">
                                <el-form-item label="">
                                    <el-date-picker type="date" placeholder="" format="yyyy  MM  dd "
                                                    value-format="yyyy-MM-dd" v-model="form.date">
                                    </el-date-picker>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-radio-group v-model="form.category">
                                        <el-radio v-for="(val, key, index) in categories" :label="key">{{ val }}</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-radio-group v-model="form.type">
                                        <el-radio v-for="(val, key, index) in types"  :label="key">{{ val }}</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input-number v-model="form.number" :step="10" :min="1" :max="1000000"
                                                     controls-position="right"></el-input-number>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input v-model="form.desc" placeholder="" style="width:60%;"></el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="onAddRecord"> </el-button>
                                </el-form-item>
                            </el-form>
                        </el-collapse-item>
                    </el-collapse>
                    <el-table
                            border
                            ref="scoreTable"
                            :data="scoreData.slice((currentPage-1) * pageSize, currentPage * pageSize)"
                            stripe
                            tooltip-effect="dark"
                            style="width: 100%">
                        <el-table-column
                                prop="date"
                                label=""
                                sortable
                                header-align="center"
                                width="180">
                        </el-table-column>
                        <el-table-column
                                label=""
                                header-align="center"
                                width="100">
                            <template slot-scope="scope">
                                {{ formatCategory(scope.row.category) }}
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="number"
                                label=""
                                header-align="center"
                                width="120">
                        </el-table-column>
                        <el-table-column
                                label=""
                                header-align="center"
                                width="100">
                            <template slot-scope="scope">
                                {{ formatType(scope.row.type) }}
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="desc"
                                label=""
                                header-align="center">
                        </el-table-column>
                        <el-table-column
                                fixed="right"
                                header-align="center"
                                label=""
                                width="150">
                            <template slot-scope="scope">
                                <el-button
                                        disabled
                                        size="mini"
                                        type="primary"
                                        @click="handleEdit(scope.$index, scope.row)"></el-button>
                                <el-button
                                        size="mini"
                                        type="danger"
                                        @click="handleDelete(scope.$index, scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination
                            background
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[10, 20, 50]"
                            :page-size="pageSize"
                            layout="prev, pager, next, sizes"
                            :total="scoreData.length">
                    </el-pagination>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<style scoped>

    .container {
        line-height: 20px;
        background-color: white;
    }

    .buddha-row-tabs {
        overflow: auto;
    }

    .el-button-group {
        margin: 10px;
    }

    .el-collapse {
        margin-bottom: 10px;
    }

    #buddha-chart {
        width: 100%;
        height: 500px;
        margin-top: 40px;
    }

    #buddha-chart-pie {
        width: 100%;
        height: 300px;
        border-bottom: solid 1px #eee;
    }

    .buddha-score-title {
        font-size: 24px;
    }

    .buddha-score {
        font-size: 28px;
        font-weight: bold;
    }

    .el-row {
        line-height: 30px;
        vertical-align: center;
        margin-bottom: 10px;
    }

    .buddha-return {
        float: right;
    }

    .el-tab-pane {
        padding: 20px;
    }

    .el-pagination {
        margin-top: 10px;
    }

</style>

<script>
  import logger from "../logger";
  import yuchg from "../base";
  import ycUtils from '../utils'
  import echarts from "echarts";
  import resize from 'vue-resize-directive'
  import CryptoJS from "crypto-js";
  import saveAs from "file-saver";

  export default {
    directives: {
      resize
    },
    data: function () {
      return {
        urls: {
          host: '/api/score',
          query: 'record',
          update: 'update'
        },
        chart: null,
        chartPie: null,
        currentPage: 1,
        pageSize: 10,
        scoreData: [],
        scoreSum: 0,
        form: {
          date: '',
          type: '1',
          number: 1,
          desc: '',
          category: '1',
        },
        types:{
          '1': '',
          '2': '',
          '3': '',
          '4': '',
          '255': '',
        },
        categories: {
          '1' : '',
          '2' : '',
          '3' : ''
        }
      }
    },
    computed: {
      queryUrl: function () {
        return this.urls.host + '/' + this.urls.query
      },
      updateUrl: function () {
        return this.urls.host + '/' + this.urls.update
      },
      chartOpt: function () {
        let series = []
        const legend = Object.keys(this.types).map((k) => {
          series.push({
            id: k,
            name: this.types[k],
            type: "line",
            stack: "",
            areaStyle: {},
            data: []
          })
          return this.types[k]
        })

        let option = {
          title: {
            text: ""
          },
          tooltip : {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          legend: {
            data: legend
          },
          grid: {
            left: '10%',
            right: '10%',
            bottom: '15%'
          },
          toolbox: {
            show: true,
            feature: {
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          dataZoom: [
            {
              show: true,
              type: 'slider',
              y: '90%',
              start: 0,
              end: 100
            }
          ],
          calculable: true,
          xAxis: [
            {
              type: "category",
              boundaryGap: false,
              data: []
            }
          ],
          yAxis: [
            {
              type: "value",
              name: '',
              axisLabel: {
                formatter: "{value}"
              }
            }
          ],
          series: series
        };
        return option;
      },
      chartPieOpt: function () {
        let option = {
          title: {
            text: '',
            left: 'left'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{b} : {c} ({d}%)"
          },
          legend: {
            bottom: 10,
            left: 'center',
            data: []
          },
          series : [
            {
              name: 'score',
              type: 'pie',
              radius : '65%',
              center: ['50%', '50%'],
              selectedMode: 'single',
              data:[],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        return option;
      }
    },
    methods: {
      onChartResize() {
        this.chart && this.chart.resize()
      },
      onChartPieResize() {
        this.chart && this.chart.resize()
      },
      onClickTab(tab) {
        if (tab.name === 'chart' && this.modified) {
          this.refreshChart()
          this.modified = false
        }
      },
      onClickReturn() {
        this.$router.go(-1)
      },
      onAddRecord() {
        if (this.form.number === 0) {
          this.$message.error('0')
          return
        }

        // , 
        const newRecord = yuchg.cloneObject(this.form)
        if (!newRecord.date || newRecord.date === '') {
          newRecord.date = new Date()
        }

        // 
        let vm = this
        ycUtils.ajaxPost({
          url: this.updateUrl,
          data: {type: 'add',record: newRecord},
          success:  (data) => {
            if (data.result === 0) {
              let inserted = false
              vm.scoreSum = data.content.score
              newRecord.id = data.content.id  // ID
              vm.scoreData.forEach((value, index) => {
                let res = yuchg.dateCompare(value.date, newRecord.date)
                if (res > 0) {
                  vm.scoreData.splice(index, 0, newRecord)
                  inserted = true
                  return false
                }
              })
              if (!inserted) {
                vm.scoreData.push(newRecord)
              }
              vm.modified = true
              vm.$message('')
            } else {
              vm.$message.error(' -' + data.err)
            }
          }
        });
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
      },
      handleSizeChange(val) {
        this.pageSize = val
        this.currentPage = 1
      },
      handleEdit(index, value) {
        this.$message('')
      },
      handleDelete(index, value) {
        let vm = this
        // 
        const realIndex = (this.currentPage - 1) * this.pageSize + index
        this.$confirm("(?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          // 
          // 
          ycUtils.ajaxPost({
            url: this.updateUrl,
            data: {
              type: 'delete',
              id: value.id
            },
            success: (data) => {
              if (data.result === 0) {
                vm.scoreData.splice(realIndex, 1)
                vm.scoreSum = data.content.score
                vm.modified = true
                this.$message('')
              } else {
                vm.$message.error(' -' + data.err)
              }
            }
          });
        }).catch(() => {
        });
      },
      fetchRecords() {
        // 
        ycUtils.ajaxGet({
          url: this.queryUrl,
          success: (data) => {
            if (data.result === 0) {
              this.scoreSum = data.content.score
              this.refreshChart(data.content.records)
            } else {
              this.$message.error(' -' + data.err)
            }
          }
        });
      },
      refreshChart(data) {
        if (data) {
          this.scoreData = data
        }

        // (
        let chart_date = yuchg.concatArray([], this.scoreData.map((v) => {
          return v.date
        }))

        // 
        const cateSum = {}
        for (let [k, v] of Object.entries(this.categories)) {
          cateSum[k] = {
            value:0, name: v
          }
        }

        const sum = {}
        const seriesData = {}
        const series = Object.keys(this.types).map((k) => {
          sum[k] = 0
          seriesData[k] = new Array(chart_date.length)
          return {
            id: k,
            name: this.types[k],
            data: seriesData[k]
          }
        })

        // 
        this.scoreData.forEach((value) => {
          let index = chart_date.indexOf(value.date)

          let v = Number(value.number)
          if (value.category !== '1') {
            v = 0 - v
          }
          cateSum[value.category].value += value.number
          sum[value.type] +=  v

          for (let key of Object.keys(this.types)) {
            seriesData[key][index] = sum[key]
          }
        })

        // 
        this.chart.setOption({
          xAxis: {
            data: chart_date
          },
          series: series
        })

        // 
        this.chartPie.setOption({
          series: [
            {
              name: 'score',
              data: Object.values(cateSum)
            }
          ]
        })
      },
      formatCategory(cate) {
        return this.categories[cate]
      },
      formatType(t) {
        return this.types[t]
      },
    },
    mounted: function () {
      this.form.date = yuchg.currentTimeString()
      this.chart = echarts.init(document.querySelector("#buddha-chart"));
      this.chart.setOption(this.chartOpt);
      this.chartPie = echarts.init(document.querySelector("#buddha-chart-pie"));
      this.chartPie.setOption(this.chartPieOpt);
      // 
      this.fetchRecords()
    }
  }
</script>
